from the people they help or from the land itself, this new knowledge gravitates towards them in the form of dust. Each particle of dust is different, as it represents a different part of Hu’s consciousness, and through their connection they implement their new gained knowledge within their culture. Through meditation and self-reflection Dust Weavers consolidate this dust into themselves and thus acquire more of Hu’s god abilities, making them more powerful warriors and wiser teachers\n        ")]),_vm._v(" "),_c('div',{staticClass:"cluster-title"},[_vm._v("\n          Cluster\n        ")]),_vm._v(" "),_c('div',{staticClass:"cluster-desc"},[_vm._v("\n             When you start playing Grow, you start into it as a part of a 5 member team, which we call “cluster”. The members of a cluster come from the same geolocation, often the same city and they need to help each other if they want to advance optimally within the game. \n        ")]),_vm._v(" "),_c('div',{attrs:{"id":"why-title"}},[_vm._v("\n          Why ?\n        ")]),_vm._v(" "),_c('div',{staticClass:"why-desc"},[_vm._v("\n             Our vision is to increase the number of lives lived well by bringing people together, empowering them to have an open discussion and giving them the tools to make a change. \n        ")]),_vm._v(" "),_c('div',{staticClass:"how-title"},[_vm._v("\n          How ?\n        ")]),_vm._v(" "),_c('div',{staticClass:"how-desc"},[_vm._v("\n             The framework behind Grow is a combination of positive psychology research, philosophy and game design. This combination aims at helping you to reflect on the kind of person you want to become and helps you to become this person by visualizing the steps that you take forward in your everyday life.  \n        ")]),_vm._v(" "),_c('div',{staticClass:"what-title"},[_vm._v("\n          What ?\n        ")]),_vm._v(" "),_vm._m(2)])}
